import uCharts from '@/components/u-charts/u-charts.js';
import {
	isJSON
} from '@/common/checker.js';
import dataJson from '@/common/data.json'

var _self;
var canvaWord = null;
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			textarea: ''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData() {
			let Word = {
				series: []
			};
			Word.series = [{
				name: "跨全端图表",
				textSize: 25,
			}, {
				name: "微信小程序",
				textSize: 20,
			}, {
				name: "支付宝小程序",
				textSize: 20,
			}, {
				name: "百度小程序",
				textSize: 20,
			}, {
				name: "QQ小程序",
				textSize: 20,
			}, {
				name: "头条小程序",
				textSize: 20,
			}, {
				name: "抖音小程序",
				textSize: 20,
			}, {
				name: "360小程序",
				textSize: 20,
			}, {
				name: "跨全端",
				textSize: 10,
			}, {
				name: "跨全端",
				textSize: 12,
			}, {
				name: "跨全端",
				textSize: 10,
			}, {
				name: "跨全端",
				textSize: 12,
			}, {
				name: "跨全端",
				textSize: 10,
			}, {
				name: "跨全端",
				textSize: 12,
			}, {
				name: "跨全端",
				textSize: 10,
			}, {
				name: "跨全端",
				textSize: 12,
			}];
			_self.textarea = JSON.stringify(Word);
			_self.showWord("canvasWord", Word);
		},
		showWord(canvasId, chartData) {
			canvaWord = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'word',
				background: '#000000',
				animation: true,
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					word: {
						type: 'normal'
					}
				}
			});
		},
		touchWord(e) {
			canvaWord.showToolTip(e, {
				format: function(item) {
					return item.name
				}
			});
		},
		changeData() {
			if (isJSON(_self.textarea)) {
				let newdata = JSON.parse(_self.textarea);
				canvaWord.updateData({
					series: newdata.series
				});
			} else {
				uni.showToast({
					title: '数据格式错误',
					image: '../../../static/images/alert-warning.png'
				})
			}
		},
		saveImage() {
			uni.canvasToTempFilePath({
				canvasId: canvaWord.opts.canvasId,
				success: function(res) {




					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function() {
							uni.showToast({
								title: '保存成功',
								duration: 2000
							});
						}
					});

				}
			});
		}
	}
}
